"use client";

import React, { useEffect, useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Separator } from '@/components/ui/separator';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { toast } from 'sonner';

interface DrawEntry {
  code: string;
  prize: string;
  date: string;
  rarity: string;
}

export default function LuckyDrawHistory() {
  const [history, setHistory] = useState<DrawEntry[]>([]);
  const [stats, setStats] = useState({
    total: 0,
    legendary: 0,
    epic: 0,
    rare: 0
  });

  const loadHistory = () => {
    try {
      const stored = localStorage.getItem('luckyDrawHistory');
      if (stored) {
        const parsedHistory = JSON.parse(stored);
        setHistory(parsedHistory);
        
        // Calculate stats
        const newStats = {
          total: parsedHistory.length,
          legendary: parsedHistory.filter((entry: DrawEntry) => entry.rarity === 'legendary').length,
          epic: parsedHistory.filter((entry: DrawEntry) => entry.rarity === 'epic').length,
          rare: parsedHistory.filter((entry: DrawEntry) => entry.rarity === 'rare').length,
        };
        setStats(newStats);
      }
    } catch (error) {
      console.error('Failed to load history:', error);
      toast.error('Failed to load redemption history');
    }
  };

  useEffect(() => {
    loadHistory();
    
    // Listen for storage events to update when new codes are redeemed
    const handleStorageChange = () => {
      loadHistory();
    };
    
    window.addEventListener('storage', handleStorageChange);
    return () => window.removeEventListener('storage', handleStorageChange);
  }, []);

  const clearHistory = () => {
    try {
      localStorage.removeItem('luckyDrawHistory');
      setHistory([]);
      setStats({
        total: 0,
        legendary: 0,
        epic: 0,
        rare: 0
      });
      toast.success('History cleared successfully!');
    } catch (error) {
      console.error('Failed to clear history:', error);
      toast.error('Failed to clear history');
    }
  };

  const getRarityBadge = (rarity: string) => {
    const rarityConfig = {
      legendary: { label: '👑 LEGENDARY', className: 'bg-gradient-to-r from-yellow-400 to-orange-500 text-white' },
      epic: { label: '💜 EPIC', className: 'bg-gradient-to-r from-purple-400 to-pink-500 text-white' },
      rare: { label: '💙 RARE', className: 'bg-gradient-to-r from-blue-400 to-cyan-500 text-white' }
    };
    
    const config = rarityConfig[rarity as keyof typeof rarityConfig] || rarityConfig.rare;
    return (
      <Badge className={config.className}>
        {config.label}
      </Badge>
    );
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString() + ' ' + date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  const getStatPercentage = (count: number) => {
    return stats.total > 0 ? ((count / stats.total) * 100).toFixed(1) : '0.0';
  };

  return (
    <Card className="bg-white/10 backdrop-blur-md border-white/20 shadow-2xl h-fit">
      <CardHeader>
        <CardTitle className="text-2xl font-bold text-white flex items-center gap-2">
          📋 Redemption History
        </CardTitle>
        <CardDescription className="text-white/80">
          Track your redeemed codes and prizes
        </CardDescription>
      </CardHeader>
      
      <CardContent className="space-y-4">
        {/* Statistics */}
        <div className="bg-black/20 rounded-lg p-4 space-y-3">
          <h4 className="text-white font-semibold text-center mb-3">🎯 Your Stats</h4>
          <div className="grid grid-cols-2 gap-2 text-sm">
            <div className="text-white/80">Total Redeemed:</div>
            <div className="text-white font-bold">{stats.total}</div>
            
            <div className="text-yellow-300">👑 Legendary:</div>
            <div className="text-white">{stats.legendary} ({getStatPercentage(stats.legendary)}%)</div>
            
            <div className="text-purple-300">💜 Epic:</div>
            <div className="text-white">{stats.epic} ({getStatPercentage(stats.epic)}%)</div>
            
            <div className="text-blue-300">💙 Rare:</div>
            <div className="text-white">{stats.rare} ({getStatPercentage(stats.rare)}%)</div>
          </div>
        </div>

        <Separator className="bg-white/20" />

        {/* History List */}
        {history.length === 0 ? (
          <div className="text-center py-8">
            <div className="text-6xl mb-4">🎟️</div>
            <p className="text-white/60 text-lg">No codes redeemed yet</p>
            <p className="text-white/40 text-sm mt-2">Enter a code to get started!</p>
          </div>
        ) : (
          <ScrollArea className="h-96">
            <div className="space-y-3">
              {history.map((entry, index) => (
                <div key={index} className="bg-black/20 rounded-lg p-3 space-y-2">
                  <div className="flex items-start justify-between gap-2">
                    <div className="flex-1 min-w-0">
                      <div className="text-white font-medium text-sm mb-1">
                        Code: {entry.code}
                      </div>
                      <div className="text-white/90 text-sm truncate">
                        {entry.prize}
                      </div>
                      <div className="text-white/60 text-xs mt-1">
                        {formatDate(entry.date)}
                      </div>
                    </div>
                    <div className="flex-shrink-0">
                      {getRarityBadge(entry.rarity)}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </ScrollArea>
        )}

        {/* Clear History Button */}
        {history.length > 0 && (
          <AlertDialog>
            <AlertDialogTrigger asChild>
              <Button 
                variant="destructive" 
                size="sm" 
                className="w-full bg-red-600 hover:bg-red-700"
              >
                🗑️ Clear History
              </Button>
            </AlertDialogTrigger>
            <AlertDialogContent className="bg-gradient-to-br from-red-900 to-pink-900 border-white/20">
              <AlertDialogHeader>
                <AlertDialogTitle className="text-white">Clear Redemption History?</AlertDialogTitle>
                <AlertDialogDescription className="text-white/80">
                  This action cannot be undone. This will permanently delete your entire redemption history and statistics.
                </AlertDialogDescription>
              </AlertDialogHeader>
              <AlertDialogFooter>
                <AlertDialogCancel className="bg-gray-600 hover:bg-gray-700 text-white border-gray-500">
                  Cancel
                </AlertDialogCancel>
                <AlertDialogAction 
                  onClick={clearHistory}
                  className="bg-red-600 hover:bg-red-700"
                >
                  Yes, Clear All
                </AlertDialogAction>
              </AlertDialogFooter>
            </AlertDialogContent>
          </AlertDialog>
        )}

        {/* Tips */}
        <div className="bg-gradient-to-r from-green-500/20 to-emerald-500/20 rounded-lg p-3 border border-green-500/30">
          <h5 className="text-green-300 font-semibold text-sm mb-2">💡 Tips</h5>
          <ul className="text-green-200 text-xs space-y-1">
            <li>• Each code can only be used once</li>
            <li>• Keep your codes safe and secure</li>
            <li>• Different codes unlock different prizes</li>
            <li>• Check back often for new codes!</li>
          </ul>
        </div>
      </CardContent>
    </Card>
  );
}
