"use client";

import React, { useState, useRef, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { AlertDialog, AlertDialogAction, AlertDialogContent, AlertDialogDescription, AlertDialogHeader, AlertDialogTitle } from '@/components/ui/alert-dialog';
import { toast } from 'sonner';

// Predefined gift codes and their prizes
const giftCodes = {
  'LUCKY2024': { name: '🚗 Brand New Car', rarity: 'legendary', color: 'from-yellow-400 to-orange-500' },
  'NEWYEAR': { name: '📺 Smart TV 65"', rarity: 'epic', color: 'from-purple-400 to-pink-500' },
  'WINTER': { name: '✈️ Dream Vacation', rarity: 'epic', color: 'from-blue-400 to-cyan-500' },
  'SPRING': { name: '🎁 $500 Gift Card', rarity: 'rare', color: 'from-green-400 to-emerald-500' },
  'SUMMER': { name: '🎮 Gaming Console', rarity: 'rare', color: 'from-indigo-400 to-purple-500' },
  'AUTUMN': { name: '💎 Jewelry Set', rarity: 'rare', color: 'from-pink-400 to-rose-500' },
};

interface DrawEntry {
  code: string;
  prize: string;
  date: string;
  rarity: string;
}

export default function LuckyDrawGame() {
  const [code, setCode] = useState('');
  const [isScratching, setIsScratching] = useState(false);
  const [result, setResult] = useState<typeof giftCodes[keyof typeof giftCodes] | null>(null);
  const [showResult, setShowResult] = useState(false);
  const [scratchPercentage, setScratchPercentage] = useState(0);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [isDrawing, setIsDrawing] = useState(false);

  useEffect(() => {
    if (canvasRef.current && result) {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext('2d');
      if (ctx) {
        ctx.fillStyle = '#CBD5E1';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
      }
    }
  }, [result]);

  const handleScratch = (e: React.MouseEvent<HTMLCanvasElement> | React.TouchEvent<HTMLCanvasElement>) => {
    if (!canvasRef.current || !result) return;

    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    const rect = canvas.getBoundingClientRect();
    const x = (e as React.MouseEvent).clientX ?? (e as React.TouchEvent).touches[0].clientX;
    const y = (e as React.MouseEvent).clientY ?? (e as React.TouchEvent).touches[0].clientY;
    
    ctx.globalCompositeOperation = 'destination-out';
    ctx.beginPath();
    ctx.arc(x - rect.left, y - rect.top, 20, 0, Math.PI * 2);
    ctx.fill();

    // Calculate scratch percentage
    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const pixels = imageData.data;
    let transparentPixels = 0;
    for (let i = 0; i < pixels.length; i += 4) {
      if (pixels[i + 3] === 0) {
        transparentPixels++;
      }
    }
    const percentage = (transparentPixels / (pixels.length / 4)) * 100;
    setScratchPercentage(percentage);

    if (percentage > 50) {
      setShowResult(true);
      canvas.style.display = 'none';
    }
  };

  const handleCodeSubmit = async () => {
    if (!code.trim()) {
      toast.error('Please enter a valid code');
      return;
    }

    setIsScratching(true);
    const upperCode = code.toUpperCase();
    
    try {
      // Check if code exists and hasn't been used
      const storedHistory = JSON.parse(localStorage.getItem('luckyDrawHistory') || '[]');
      if (storedHistory.some((entry: DrawEntry) => entry.code === upperCode)) {
        toast.error('This code has already been used!');
        setIsScratching(false);
        return;
      }

      const prize = giftCodes[upperCode as keyof typeof giftCodes];
      if (!prize) {
        toast.error('Invalid code. Please try again!');
        setIsScratching(false);
        return;
      }

      setResult(prize);
      
      // Save to history
      const newEntry: DrawEntry = {
        code: upperCode,
        prize: prize.name,
        date: new Date().toISOString(),
        rarity: prize.rarity
      };
      storedHistory.unshift(newEntry);
      if (storedHistory.length > 50) {
        storedHistory.splice(50);
      }
      localStorage.setItem('luckyDrawHistory', JSON.stringify(storedHistory));
      window.dispatchEvent(new Event('storage'));

    } catch (error) {
      toast.error('An error occurred. Please try again.');
      console.error('Error:', error);
    }
  };

  const getRarityBadge = (rarity: string) => {
    const badges = {
      legendary: '👑 LEGENDARY',
      epic: '💜 EPIC',
      rare: '💙 RARE'
    };
    return badges[rarity as keyof typeof badges] || '🤍 COMMON';
  };

  return (
    <Card className="bg-white/10 backdrop-blur-md border-white/20 shadow-2xl">
      <CardHeader className="text-center">
        <CardTitle className="text-3xl font-bold text-white mb-2">
          🎟️ Scratch Card
        </CardTitle>
        <CardDescription className="text-white/80 text-lg">
          Enter your code and scratch to reveal your prize!
        </CardDescription>
      </CardHeader>
      
      <CardContent className="space-y-6">
        {/* Code Input */}
        <div className="space-y-4">
          <Input
            type="text"
            placeholder="Enter your code here"
            value={code}
            onChange={(e) => setCode(e.target.value.toUpperCase())}
            className="bg-white/20 border-white/30 text-white placeholder:text-white/50"
            disabled={isScratching}
          />
          <Button 
            onClick={handleCodeSubmit} 
            disabled={isScratching || !code.trim()} 
            className="w-full h-12 text-lg font-bold bg-gradient-to-r from-pink-500 to-purple-600 hover:from-pink-600 hover:to-purple-700 transform hover:scale-105 transition-all duration-200 shadow-lg"
          >
            Reveal Prize! 🎁
          </Button>
        </div>

        {/* Scratch Card Area */}
        {result && !showResult && (
          <div className="relative w-full aspect-video bg-gradient-to-r from-purple-500 to-pink-500 rounded-lg overflow-hidden">
            <canvas
              ref={canvasRef}
              width={400}
              height={300}
              className="absolute inset-0 w-full h-full cursor-pointer touch-none"
              onMouseDown={() => setIsDrawing(true)}
              onMouseUp={() => setIsDrawing(false)}
              onMouseMove={(e) => isDrawing && handleScratch(e)}
              onTouchStart={() => setIsDrawing(true)}
              onTouchEnd={() => setIsDrawing(false)}
              onTouchMove={(e) => isDrawing && handleScratch(e)}
            />
            <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
              <div className="text-white text-xl font-bold">Scratch here to reveal your prize!</div>
            </div>
          </div>
        )}

        {/* Instructions */}
        <div className="text-white/70 text-sm text-center space-y-1">
          <p>🎯 Enter your code and scratch to reveal your prize</p>
          <p>✨ Each code can only be used once</p>
          <p>🎁 Different codes unlock different prizes</p>
        </div>
      </CardContent>

      {/* Result Dialog */}
      <AlertDialog open={showResult} onOpenChange={setShowResult}>
        <AlertDialogContent className="bg-gradient-to-br from-purple-900 to-indigo-900 border-white/20">
          <AlertDialogHeader>
            <AlertDialogTitle className="text-2xl text-center text-white">
              🎉 Congratulations!
            </AlertDialogTitle>
            <AlertDialogDescription className="text-center">
              {result && (
                <div className="space-y-4">
                  <div className={`p-6 rounded-lg bg-gradient-to-r ${result.color} text-white text-center shadow-lg`}>
                    <div className="text-2xl font-bold mb-2">{result.name}</div>
                    <div className="text-lg opacity-90">
                      {getRarityBadge(result.rarity)}
                    </div>
                  </div>
                  <div className="text-white/80">
                    You've won an amazing prize!
                  </div>
                </div>
              )}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogAction 
            onClick={() => {
              setShowResult(false);
              setIsScratching(false);
              setCode('');
              setResult(null);
            }}
            className="bg-gradient-to-r from-green-500 to-emerald-600 hover:from-green-600 hover:to-emerald-700"
          >
            Claim Prize! 🎊
          </AlertDialogAction>
        </AlertDialogContent>
      </AlertDialog>
    </Card>
  );
}
